<template>
  <!-- 里面的内容必须是字符串 -->
  <div v-theme:column="'narrow'" id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索">
    <div v-for="blog in filteredBlogs" class="single-blog" :key="blog.Id">
      <router-link v-bind:to="'/blog/' + blog.id">
        <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
      </router-link>
      <!-- <article>{{blog.body | snippet}}</article> -->
      <article>{{blog.content | snippet}}</article>
    </div>
  </div>
</template>

<script>
export default {
  name: "show-blogs",
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    // this.$http.get("http://jsonplaceholder.typicode.com/posts").then(data => {
    //   this.blogs = data.body.slice(0, 10);
    // });
    // this.$http.get("./../static/posts.json").then(data => {
    //   this.blogs = data.body.slice(0, 10);
    // });
    this.$http
      .get("https://fir-c12f2.firebaseio.com/posts.json")
      .then(data => {
        console.log(data);
        return data.json();
      })
      .then(data => {
        var blogsArray = [];
        for (let key in data) {
          data[key].id = key;
          blogsArray.push(data[key]);
        }
        this.blogs = blogsArray;
      });
    // axios
    //   .get("/posts.json")
    //   .then(function(data) {
    //     return data.data;
    //   })
    //   .then(data => {
    //     var blogsArray = [];
    //     for (let key in data) {
    //       data[key].id = key;
    //       blogsArray.push(data[key]);
    //     }
    //     this.blogs = blogsArray;
    //   });
  },
  //自定义指令
  directives: {
    rainbow: {
      bind(el, binding, vnode) {
        el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8);
      }
    }
  },
  filters: {
    //两种方式都可以
    // "to-uppercase": function(value) {
    //   return value.toUpperCase();
    // }
    toUppercase(value) {
      return value.toUpperCase();
    }
  },
  computed: {
    filteredBlogs: function() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  }
};
</script>

<style >
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}

.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
  border: 1px dotted #aaa;
}

#show-blogs a {
  color: #444;
  text-decoration: none;
}

input[type="text"] {
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>


